// BASIC
import React, {Component} from 'react';
import styled, {css, keyframes} from 'styled-components'
import {Link, useHistory} from 'react-router-dom'
import ReactHtmlParser from 'react-html-parser'
// CONFIG
import {db, au, fu, st} from '../../Config/firebase'
// COMPONENTS
// import Achievements from './components/Achievements'
// ICONS
import {FaPencilAlt, FaTrophy, FaReply, FaUserPlus} from 'react-icons/fa'
import {TiChartLine, TiChartPie, TiPlus} from 'react-icons/ti'
// KEYFRAMES
import { editOpacity } from '../Styles/Keyframes'
import points from './BlankPointsObject';
import hello from '../Images/hello.png'

const UserProfileSite = styled.div`
	width: 100vw;
	height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
`
const LoginTitle = styled.header`
	font-size: 35px;
	font-weight: bold;
	text-align: center;
	margin: 30px 0;
`
const Avatar = styled.div`
	width: 200px;
	min-height: 200px;
	position: relative;
	border-radius: 100%;
	overflow: hidden;
	background: url("${props => props.avatar}") no-repeat center center;
	background-size: cover;
	::after {
		/* content: '${props => props.progress > 20 ? `${props.progress}%` : ''}'; */
		content: '${props => `${Math.floor(props.progress)}%`}';
		width: ${props => `${props.progress}%`};
		height: 100%;
		position: absolute;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		top: 0;
		left: 0;
		padding-right: ${props => props.progress ? '10px' : '0'};
		background-color: var(--color-light);
		color: var(--color-dark);
		opacity: 0.5;
		font-weight: bold;
		font-size: 30px;
		transition: width 0.3s ease;
	}
	@media (min-width: 800px) {
		width: 250px;
		min-height: 250px;
	}
`
const Edit = styled(FaPencilAlt)`
	display: none;
	position: absolute;
	top: 0;
	left: 0;
	width: 200px;
	height: 200px;
	padding: 80px;
	background-color: var(--color-bg);
	animation: ${editOpacity} 0.2s ease;
	cursor: pointer;
	${props =>
		props.preview &&
		css`
			${Avatar}:hover & {
				display: block;
				opacity: 0.5;
			}
		`
	};
	@media (min-width: 800px) {
		width: 250px;
		height: 250px;
		padding: 100px;
	}
`
const UserContent = styled.div`
	position: relative;
	border: 1px solid var(--color-secondary);
	width: 90vw;
	max-width: 1000px;
	height: calc(100vh - 480px);
	min-height: 100px;
	padding: 10px;
	margin: 20px;
	border-radius: 20px;
	text-align: center;
	overflow-y: scroll;
	::-webkit-scrollbar {
		width: 0;
	}
	@media (min-width: 800px) {
		height: calc(100vh - 550px);
	}
`
const Biogram = styled.div`
	padding: 0 20px;
	:focus {
		outline: none;
	}
`
const Confirm = styled(TiPlus)`
	position: absolute;
	top: 10px;
	right: 10px;
	font-size: 25px;
	color: var(--color-decorative);
	opacity: 0;
	transition: all 0.2s ease;
	${props =>
		props.show &&
		css`
			opacity: 1;
			:hover {
				cursor: pointer;
			}
		`
	};
`
// "...jeśli nie poświęcasz czasu na rodzenie się, czas zajmuje ci umieranie..."
// -Steve Jobs
const AddAdmin = styled.form`
	position: absolute;
	bottom: 0;
	left: 50%;
	transform: translateX(-50%);
	display: none;
	align-items: center;
	background-color: var(--color-bg);
	/* height: 30px; */
	${props =>
		props.preview &&
		css`
			display: flex;
	`}
`
const Email = styled.input`
	background-color: transparent;
	border: none;
	border-bottom: 2px solid var(--color-decorative);
	margin: 5px;
	:focus {
		outline: none;
	}
	::placeholder {
		transition: all 0.2s ease;
		color: var(--color-secondary);
		font-family: 'Ubuntu', sans-serif;
		font-size: 18px;
	}
	:focus::placeholder {
		opacity: 0;
		transform: translateX(30px);
	}
`
const Submit = styled.button`
	background-color: transparent;
	border: none;
	font-size: 30px;
	color: var(--color-decorative);
	:hover {
		cursor: pointer;
	}
`
const Input = styled.input`
	display: none;
`

const Achievements = styled.div`
	width: 90vw;
	max-width: 1000px;
	display: flex;
	justify-content: space-evenly;
`
const Achievement = styled.div`
	font-size: 40px;
	color: var(--color-decorative);
	transition: all 0.3s ease;
	:hover {
		cursor: pointer;
		opacity: 0.5;
	}
	@media (min-width: 800px) {
		font-size: 50px;
	}
`
const Go = styled(Link)`
	text-decoration: none;
	color: var(--color-decorative);
`
const Return = styled.div`
	width: 90vw;
	max-width: 1000px;
	margin: 20px 0;
	display: flex;
	justify-content: space-evenly;
	align-items: center;
`
const Back = styled.div`
	font-size: 30px;
	transition: all 0.3s ease;
	:hover {
		cursor: pointer;
		opacity: 0.5;
	}
`
const LogIn = styled.button`
	position: relative;
	display: none;
	justify-content: space-evenly;
	align-items: center;
	line-height: 150%;
	min-width: 180px;
	font-size: 20px;
	padding: 10px 20px;
	background-color: transparent;
	border-radius: 20px;
	border: 2px solid ${props => `var(--color-${props.color})`};
	color: var(--color-primary);
	transition: all 0.5s ease;
	overflow: hidden;
	::before {
		content: '';
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		width: 100%;
		height: 100%;
		background-color: ${props => `var(--color-${props.color})`};
		opacity: 0.5;
		filter: blur(5px);
		transform: translateX(-250px) skewX(-15deg);
		z-index: 2;
	}
	:hover {
		cursor: pointer;
		background-color: ${props => `var(--color-${props.color})`};
		color: ${props => props.color === 'main' || props.color === 'decorative' ? 'var(--color-bg)' : 'var(--color-light)'};
		::before {
			transform: translate(250px) skewX(-15deg);
			opacity: 0.6;
			transition: all 0.7s ease;
		}
	}
	:focus {
		outline: none
	}
	${props =>
		props.preview &&
		css`
			display: flex;
		`
	};
`

class UserProfile extends Component {
	state = {
		info: null,
		isAdmin: false,
		isOwner: false,
		previewUserAchievements: false,
		percentage: 0,
		biogramEdit: false
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if(user) {
				console.log(user);
				// sprawdź, czy ma uid
				db.collection('users').doc(user.uid).get().then(snap => {
					if(!snap.data() || !snap.data().info) {
						const nick = prompt("Podaj swój nick:", `${user.displayName}`);
						db.collection('users').doc(user.uid).set({
							info: {
								uid: user.uid,
								nick: nick,
								biogram: prompt("Uzupełnij biogram (opis):", "Jestem nowym użytkownikiem EasyWord!"),
								email: user.email
							}
						})
						if (user.photoURL) {
							db.collection('users').doc(user.uid).update({
								'info.avatar': user.photoURL
							})
						}
						else {
							db.collection('users').doc(user.uid).update({
								'info.avatar': 'https://firebasestorage.googleapis.com/v0/b/easyword.appspot.com/o/users%2Fjakubzlezena%40wp.pl%2Fhello.png?alt=media&token=667f08aa-7c17-4976-b208-96565dcf9175'
							})
						}
					}
					else {
						if (!snap.data().info.nick) {
							db.collection('users').doc(user.uid).update({
								'info.nick': prompt("Podaj swój nick:", `${user.displayName}`)
							})
						}
						if (!snap.data().info.biogram) {
							db.collection('users').doc(user.uid).update({
								'info.biogram': prompt("Uzupełnij biogram (krótki opis):", "Jestem nowym użytkownikiem EasyWord!")
							})
						}
						if (!snap.data().info.email) {
							db.collection('users').doc(user.uid).update({
								'info.email': user.email
							})
						}
					}
					if (user.uid === this.props.match.params.id) {
						user.getIdTokenResult().then(idTokenResult => {
							if (idTokenResult.claims.admin) {
								this.setState({isAdmin: true})
							}
							else {
								this.setState({isAdmin: false})
							}
							user.admin = idTokenResult.claims.admin
						})
						db.collection('users').doc(user.uid).onSnapshot(snap => {
							const {nick, biogram, uid, email, avatar} = snap.data().info;
							this.setState({
								info: {
									nick, biogram, uid, email, avatar
								},
								points: snap.data()['easy-word'],
								isOwner: true
							})
						});
					}
					else {
						db.collection('users').doc(this.props.match.params.id).onSnapshot(snap => {
							const {nick, biogram, uid, email, avatar} = snap.data().info;
							this.setState({
								info: {
									nick, biogram, uid, email, avatar
								}
							})
							if (snap.data()['easy-word']) {
								this.setState({
									points: snap.data()['easy-word']
								})
							}
						});
					}
				})
			}
			else {
				this.props.history.push(`/login`);
			}
		})
	}
	addAdminCloudFunction = (e) => {
		e.preventDefault();
		const adminEmail = document.getElementById('adminEmail').value;
		const addAdminRole = fu.httpsCallable('addAdminRole');
		addAdminRole({email: adminEmail}).then(result => {
			console.log(result);
		})
	}
	logOut = () => {
		au.signOut();
		this.props.history.push(`/login`);
	}
	editAvatar = () => {
		if (this.state.isOwner) {
			document.getElementById('fileButton').click();
		}
	}
	handleUserAchievements = () => {
		this.state.previewUserAchievements ? this.setState({previewUserAchievements: false}) : this.setState({previewUserAchievements: true})
		
	}
	handleChange = (e) => {
		const file = e.target.files[0];
		const storageRef = st.ref(`users/${this.state.info.email}/${file.name}`);
		const task = storageRef.put(file);
		task.on('state_changed',
		(snap) => {
			this.setState({percentage: (snap.bytesTransferred / snap.totalBytes) * 100});
		}, err => {
			console.log(err);
		}, () => {
			st.ref(`users/${this.state.info.email}`).child(file.name).getDownloadURL().then(url => {
				console.log(url);
				db.collection('users').doc(this.state.info.uid).update({
					'info.avatar': url
				})
				this.setState({percentage: 0})
			})
		})
	}
	editBiogram = () => {
		this.setState({biogramEdit: true})
	}
	updateBiogram = () => {
		const html = document.getElementById('biogramEditor').innerHTML;
		db.collection('users').doc(this.state.info.uid).update({
			'info.biogram': html
		})
		this.setState({biogramEdit: false})
	}
	render() {
		return (
			<UserProfileSite>

			


				{!this.state.info ? '...' : <>
					{/* {this.props.match.params.id === this.state.info.uid ? <> */}
						<LoginTitle>{this.state.info.nick}</LoginTitle>
						<Avatar avatar={this.state.info.avatar} progress={this.state.percentage}>
							<Edit preview={this.state.isOwner} onClick={this.editAvatar} />
							<Input type="file" accept="image/*" onChange={this.handleChange} id="fileButton"/>
						</Avatar>
						<UserContent>
							<Biogram id="biogramEditor" contentEditable={this.state.isOwner} onInput={this.editBiogram}>
								{ReactHtmlParser(this.state.info.biogram)}
							</Biogram>
							<Confirm onClick={this.updateBiogram} show={this.state.biogramEdit} />
							<AddAdmin onSubmit={this.addAdminCloudFunction} className="admin" preview={this.state.isAdmin}>
								<Email type="email" placeholder="User email" id="adminEmail" required />
								<Submit type="submit"><FaUserPlus /></Submit>
							</AddAdmin>
						</UserContent>
						<Achievements>
							<Achievement>
								<TiChartLine />
								{/* {!this.state.points ? '...' : this.state.points.experience} XP */}
							</Achievement>
							<Achievement>
								<Go to="/ranking"><FaTrophy /></Go>
							</Achievement>
							<Achievement>
								<TiChartPie />
							</Achievement>
						</Achievements>
						<Return>
							<Back onClick={() => this.props.history.goBack()}><FaReply /></Back>
							<LogIn color="main" onClick={this.logOut} preview={this.state.isOwner}>Wyloguj się</LogIn>
						</Return>
						{/* </> : 'inny profil'} */}
				</>}
				{/* <button onClick={this.handleUserAchievements}>Zobacz swoje wyniki</button> */}
				{/* <button>Przejdź do tablicy wyników</button> */}
				{/* <Achievements preview={this.state.previewUserAchievements} points={this.state.points} back={this.handleUserAchievements} /> */}
			</UserProfileSite>
		);
	}
}

export default UserProfile;
